<template>
  <div class='serviceBox'>
    <el-select v-model="province.id" @change="provinceChange" clearable>
      <el-option v-for='(val,key) in province.box' :key='key' :value='key' :label='val'></el-option>
    </el-select>
    <el-select v-model="city.id" @change="cityChange" clearable>
      <el-option v-for='(val,key) in city.box' :key='key' :value='key' :label='val'></el-option>
    </el-select>
    <el-select v-model="area.id" @change="areaChange" clearable>
      <el-option v-for='(val,key) in area.box' :key='key' :value='key' :label='val'></el-option>
    </el-select>
  </div>
</template>

<script>
export default {
  data() {
    return {
      areaObj: {},
      province: {
        id: "",
        name: "",
        box: {}
      },
      city: {
        id: "",
        name: "",
        box: {}
      },
      area: {
        id: "",
        name: "",
        box: {}
      }
    };
  },
  props: {
    data: {
      type: Object,
      default: () => {
        return {
          province: "",
          city: "",
          area: ""
        };
      }
    }
  },
  methods: {
    initArea() {
      if (this.data.province) {
        this.province.id = this.data.province;
        this.provinceChange();
      } else {
        this.province.id = "";
      }
      if (this.data.city) {
        this.city.id = this.data.city;
        this.cityChange();
      } else {
        this.city.id = "";
      }
      if (this.data.area) {
        this.area.id = this.data.area;
        this.areaChange();
      } else {
        this.area.id = "";
        this.areaChange();
      }
    },
    provinceChange() {
      this.province.name = this.province.box[this.province.id];
      this.city.box = this.areaObj[this.province.id];
      this.city.id = "";
      this.area.id = "";
      this.change();
    },
    cityChange() {
      this.city.name = this.city.box[this.city.id];
      this.area.box = this.areaObj[this.city.id];
      this.area.id = "";
      this.change();
    },
    areaChange() {
      this.area.name = this.area.box[this.area.id];
      this.change();
    },
    change() {
      this.$emit("change", {
        province: this.province,
        city: this.city,
        area: this.area
      });
    },
    // 处理后台的数据
    dealArea(obj) {
      var result = {};
      for (var i in obj) {
        var cobj = {};
        obj[i].forEach(twoVal => {
          cobj[twoVal[0]] = twoVal[1];
        });
        result[i] = cobj;
      }
      this.areaObj = result;
      this.province.box = this.areaObj[0];
    }
  },
  mounted() {
    this.$axMethod.serviceGetAreaList().then(({ data }) => {
      this.dealArea(data.data);
      this.initArea();
    });
  },
  watch: {}
};
</script> 

<style lang="less" scoped>
.serviceBox {
  display: inline-block;
  /deep/ .el-select {
    .el-input {
      width: 100px;
    }
  }
}
</style>