<script>
import editReceiverVue from "./editReceiverVue";
import { paramsModule, tableHeader } from "./config/confirmOrder.js";
export default {
  mixins: [tableHeader],
  name: "jybdorderJybdorderAdd",
  data() {
    return {
      isShowEditReceiverVue: false,
      activeREeceiver: -1,
      account_rebate_balance: 0,
      tableData: [],
      params: this.$publicFution.deepCopy(paramsModule),
      receiverBox: [],
      form: {},
      currentRow: {},
    };
  },
  props: {
    data: {
      type: Object,
      default: () => {},
    },
  },
  created() {
    this.getReceiverList();
    this.initData();
  },
  methods: {
    initData() {
      if (this.data && this.data.type !== undefined) {
        var obj;
        if (this.data.data) {
          var idBox = [];
          obj = {
            by_cart: this.data.type,
            data_list: this.data.data.map((val) => {
              idBox.push(
                this.data.type === 1 ? val.cart_id : val.shop_goods_id
              );
              return {
                id: this.data.type === 1 ? val.cart_id : val.shop_goods_id,
                num: val.goods_num,
                goods_name: val.goods_name,
                inventory_goods_id: val.inventory_goods_id,
              };
            }),
          };
          this.params.cart_ids = idBox.join(",");
          obj.data_list = JSON.stringify(obj.data_list);
        } else {
          obj = {
            by_cart: this.data.type,
            all_receipt: 1,
          };
        }
        this.$axMethod.getConfirmOrder(obj).then(({ data }) => {
          if (data.code == 200) {
            this.params.buy_goods_list = data.data.buy_goods_list;
            this.account_rebate_balance = data.data.account_rebate_balance;
            this.params.by_cart = this.data.type;
            this.params.cart_ids = data.data.cart_id;
            this.params.pay_phone = data.data.member_mobile;
            this.computeToalPrice();
          } else {
            this.goBack();
          }
        });
      }
    },
    // 获取数字
    getNum(val) {
      if (isNaN(val)) {
        return 0;
      }
      return Number(val);
    },
    // 计算总价
    computeToalPrice() {
      let goods_price_total = 0;
      var freight = 0;
      this.params.buy_goods_list.forEach((val) => {
        freight += this.getNum(val.freight_store_total);
        goods_price_total += this.getNum(val.goods_price_total);
      });
      this.params.order_total_amount = goods_price_total;
      this.params.freight_total_total = freight;
      this.params.amountsPayable =
        this.params.order_total_amount -
        this.getNum(this.params.rebate_price) +
        this.getNum(this.params.freight_total_total);
    },
    // 返利改变
    rebateChange() {
      this.$publicFution.toFixedTwo(this.params, "rebate_price", {
        min: 0,
        max:
          this.params.order_total_amount > this.account_rebate_balance
            ? this.account_rebate_balance
            : this.params.order_total_amount,
        message: "返利不能大于订单总金额和余额",
      });
      this.params.amountsPayable =
        this.params.order_total_amount -
        this.getNum(this.params.rebate_price) +
        this.getNum(this.params.freight_total_total);
    },
    sumbit() {
      if (this.activeREeceiver == -1) {
        this.$message({
          message: "请选择收货信息",
          type: "warning",
        });
        return false;
      }
      var obj = this.$publicFution.filter(paramsModule, this.params);
      obj.goods_list = [];
      obj.store_list = [];
      this.params.buy_goods_list.forEach((val) => {
        obj.goods_list = obj.goods_list.concat(val.goods_list);
        obj.order_type = this.data.order_type || 1;
        obj.store_list.push({
          store_id: val.store_id,
          order_message: val.order_message,
        });
      });
      obj.goods_list = JSON.stringify(obj.goods_list);
      obj.store_list = JSON.stringify(obj.store_list);
      // console.log(JSON.stringify(obj));
      // return false;
      // this.topay();
      this.$axMethod.createServiceOrder(obj).then(({ data }) => {
        if (data.code == 200) {
          if (this.params.pay_way) {
            // this.topay(data.data);
            //在线支付
            const queryParams = {
              cashier_token: data.data.pay_data.cashier_token, // 收银台支付token
              pay_trade_no: data.data.pay_data.pay_trade_no, // 支付单号
              token: data.data.pay_data.user_pay_token // 用户token
            }
            this.$router.push({ 
              name: "PlaceholderPaycenter",
              params: queryParams
            });
          } else {
            this.$router.push({
              path: "/MoneyManagement/Checkstand",
              query: {
                id: data.data.pay_sn.join(","),
                phone: this.params.pay_phone,
              },
            });
          }
          // console.log(data);
          // this.$message.success(data.msg);
          // this.$router.push({
          //   path: "/MoneyManagement/Checkstand",
          //   query: { id: data.data.pay_sn.join(","), phoneStaeck: false },
          // });
          // this.goBack();
        }
      });
    },

    goBack() {
      this.$emit("close");
    },
  },
  components: {
    editReceiverVue,
  },
};
</script>

