<template>
  <div class="editReceiverVue">
    <vxe-modal v-model="value" class="editReceiverVue" title='添加联系人信息' @hide="$emit('input', false)" id="inReceiptSelectOrder" width="600" :esc-closable='true' destroy-on-close height="550" resize remember storage transfer>
      <div class="el_tree_box">
        <el-form :model="params" class="demo-form-inline" ref="params" label-width='106px'>
          <el-form-item label="智能地址识别：" size="mini">
            <el-input type="textarea" size="medium" :rows='5' placeholder="粘贴整段地址，自动识别姓名、电话和地址。例如：王西西  13200000000 北京市海淀区东升科技园
" v-model="textarea" style="width:310px">
            </el-input>
          </el-form-item>
          <el-form-item label="" size="mini">
            <el-button type="primary" @click="Identify">识别</el-button>
          </el-form-item>
          <el-form-item label="收货人姓名：" size="mini" prop="consignee_name" :rules="{ required:true, message: '请输入联系人姓名',tiggle: 'blur' }">
            <el-input v-model="params.consignee_name" placeholder="请输入联系人姓名" clearable></el-input>
          </el-form-item>
          <el-form-item label="手机号码：" size="mini" prop="phone" :rules="rules.phone">
            <el-input v-model="params.phone" maxlength="11" show-word-limit placeholder="请输入联系人手机号" clearable></el-input>
          </el-form-item>
          <el-form-item label="所属区域：" size="mini" prop="area_id" :rules="{ required:true, message: '请选择所属区域',tiggle: 'blur' }">
            <getAreaList ref='area' @change="areaChange" :data='{province: params.province_id,city: params.city_id,area: params.area_id}' />
          </el-form-item>
          <el-form-item label="详细地址：" size="mini" prop="address" :rules="{ required:true, message: '请输入详细地址',tiggle: 'blur' }">
            <el-input type='textarea' :rows='5' style="width:310px" v-model="params.address" maxlength="100" placeholder="请输入详细地址" clearable></el-input>
          </el-form-item>
          <el-form-item label="设为默认地址：" size="mini">
            <el-radio-group v-model="params.is_default">
              <el-radio label="1">是</el-radio>
              <el-radio label="0">否</el-radio>
            </el-radio-group>
          </el-form-item>

        </el-form>
      </div>
      <div class="dialog-footer">
        <el-button type="primary" @click="submit">确定</el-button>
        <el-button @click="$emit('input', false)">取 消</el-button>
      </div>
    </vxe-modal>
  </div>
</template>

<script>
import getAreaList from "common/components/plugin/service/areaSelect";
export default {
  data() {
    return {
      params: {
        is_default: "0",
      },
      textarea: "",
    };
  },
  watch: {
    value(newVal) {
      if (newVal === true) {
        this.initData();
      }
    },
  },
  props: {
    value: null,
    data: {
      type: Object,
      default: () => {},
    },
  },
  created() {
    this.paramsModule = {
      consignee_name: "",
      phone: "",
      province_id: "",
      city_id: "",
      area_id: "",
      is_default: "0",
      region: "",
      address: "",
    };
    this.params = this.$publicFution.deepCopy(this.paramsModule);
  },
  methods: {
    areaChange({ province, city, area }) {
      this.params.province_id = province.id;
      this.params.city_id = city.id;
      this.params.area_id = area.id;
      this.params.region = province.name + " " + city.name + " " + area.name;
    },
    Identify() {
      if (!this.textarea) this.$message.error("智能地址识别为空");
      this.$axMethod
        .getAddressInfo({ address: this.textarea })
        .then(({ data }) => {
          let {
            area_info,
            name: consignee_name,
            mobile: phone,
            detail: address,
          } = data.data;
          let aretinfo = ["province_id", "city_id", "area_id"];
          for (let index in area_info) {
            this.params[aretinfo[index - 1]] =
              area_info[index].area_id.toString();
          }
          this.params = Object.assign({}, this.params, {
            consignee_name,
            phone,
            address,
          });
          this.$nextTick(() => {
            this.$refs.area.initArea();
          });
          console.log(this.params);
        });
    },
    submit() {
      this.$refs.params.validate((valid) => {
        if (valid) {
          var url = "serviceAddAddress";
          if (this.data.address_id) {
            url = "serviceEditAddress";
          }
          this.$axMethod[url](this.params).then(({ data }) => {
            if (data.code === 200) {
              this.$message.success(data.msg);
              this.$emit("success", data);
            }
          });
        }
      });
    },
    initData() {
      if (this.data && this.data.address_id) {
        this.params = Object.assign({}, this.params, this.data);
        this.$nextTick(() => {
          this.$refs.area.initArea();
        });
      } else {
        this.params = this.$publicFution.deepCopy(this.paramsModule);
        this.$nextTick(() => {
          this.$refs.params.clearValidate();
          this.$refs.area.initArea();
        });
      }
    },
  },
  components: {
    getAreaList,
  },
};
</script>

<style scoped lang='less'>
.editReceiverVue {
  /deep/ .el-form {
    .el-form-item--mini.el-form-item {
      width: auto;
    }
  }
}
</style>